@keyframes stopwatch-hend-animation {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: relative;
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite stopwatch-hend-animation;
  position: absolute;
  bottom: 50%;
}

.stopwatch__minute-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite stopwatch-hend-animation;
  position: absolute;
  bottom: 50%;
}

.stopwatch--speed-up .stopwatch__minute-hand {
  animation: 600s steps(60, end) infinite stopwatch-hend-animation;
}

.stopwatch--speed-up .stopwatch__seconds-hand {
  animation: 10s linear infinite stopwatch-hend-animation;
}
